import { NextResponse } from 'next/server';
import dbConnect from '@/lib/db';
import CourseRequest from '@/models/CourseRequest';

export async function GET() {
  await dbConnect();

  try {
    const requests = await CourseRequest.find({}).sort({ createdAt: -1 });
    return NextResponse.json({ requests }, { status: 200 });
  } catch (error: any) {
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}

export async function PATCH(req: Request) {
  await dbConnect();

  try {
    const { requestId, status } = await req.json();

    if (!requestId || !status) {
      return NextResponse.json({ error: 'Missing required fields' }, { status: 400 });
    }

    const updatedRequest = await CourseRequest.findByIdAndUpdate(
      requestId,
      { status },
      { new: true }
    );

    if (!updatedRequest) {
      return NextResponse.json({ error: 'Request not found' }, { status: 404 });
    }

    // TODO: If status is 'APPROVED', we should create the actual Course record here.
    // For now, we just update the request status.

    return NextResponse.json({
      message: `Request ${status.toLowerCase()} successfully`,
      request: updatedRequest,
    }, { status: 200 });

  } catch (error: any) {
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}
